.login {
  width: 30%;
  height: 80%;
  position: fixed;
  margin-left: 765px;
  margin-top: 70px;
  border-radius: 5px;
  box-shadow: 0px 0 20px #726669;
  background-color: #ddf3ed;
}
.login .ltitle {
  text-align: center;
  margin-top: 100px;
}
.login .lform {
  margin-top: 5px;
  margin-right: 70px;
}
.login .lform .lsubmit {
  display: flex;
}
.login .lform .lsubmit .lbtn {
  width: 261px;
  margin-left: 30px;
}
.login .lform .ljump {
  margin-left: 125px;
}
.login .thread {
  display: flex;
  opacity: 0.4;
}
.login .thread .left {
  margin-left: 40px;
}
.login .logways {
  display: flex;
  margin-top: 30px;
  margin-left: 120px;
}
.foot {
  margin-left: 580px;
  position: absolute;
  bottom: 0;
}
.lwrapter {
  border-radius: 5px;
  box-shadow: 0px 0 20px #726669;
  width: 30%;
  height: 80%;
  position: fixed;
  margin-top: 70px;
  margin-left: 300px;
  opacity: 0.8;
  background: linear-gradient(to bottom right, #e9b659, #d0a5e9);
  background: -webkit-linear-gradient(to bottom right, #50a3a2, #53e3a6);
}
.lwrapter .lone {
  color: azure;
  margin-top: 40px;
  margin-left: 70px;
}
.lwrapter .lcontent {
  width: 300px;
  margin-left: 50px;
  line-height: 200%;
}
.lwrapter .lout {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
}
.lwrapter .lout .lin {
  list-style-type: none;
  display: block;
  position: absolute;
  bottom: -120px;
  width: 15px;
  height: 15px;
  z-index: -8;
  background-color: rgba(255, 255, 255, 0.15);
  animation: square 25s infinite;
  -webkit-animation: square 25s infinite;
}
.lwrapter .lout .lin:nth-child(1) {
  left: 0;
  animation-duration: 10s;
  -moz-animation-duration: 10s;
  -o-animation-duration: 10s;
  -webkit-animation-duration: 10s;
}
.lwrapter .lout .lin:nth-child(2) {
  width: 40px;
  height: 40px;
  left: 10%;
  animation-duration: 15s;
  -moz-animation-duration: 15s;
  -o-animation-duration: 15s;
  -webkit-animation-duration: 15s;
}
.lwrapter .lout .lin:nth-child(3) {
  left: 20%;
  width: 25px;
  height: 25px;
  animation-duration: 12s;
  -moz-animation-duration: 12s;
  -o-animation-duration: 12s;
  -webkit-animation-duration: 12s;
}
.lwrapter .lout .lin:nth-child(4) {
  width: 50px;
  height: 50px;
  left: 30%;
  animation-delay: 3s;
  animation-duration: 12s;
  -moz-animation-duration: 12s;
  -o-animation-duration: 12s;
  -webkit-animation-duration: 12s;
}
.lwrapter .lout .lin:nth-child(5) {
  width: 60px;
  height: 60px;
  left: 40%;
  animation-duration: 10s;
  -moz-animation-duration: 10s;
  -o-animation-duration: 10s;
  -webkit-animation-duration: 10s;
}
.lwrapter .lout .lin:nth-child(6) {
  width: 75px;
  height: 75px;
  left: 50%;
  animation-delay: 7s;
}
.lwrapter .lout .lin:nth-child(7) {
  left: 60%;
  animation-duration: 8s;
  -moz-animation-duration: 8s;
  -o-animation-duration: 8s;
  -webkit-animation-duration: 8s;
}
.lwrapter .lout .lin:nth-child(8) {
  width: 90px;
  height: 90px;
  left: 70%;
  animation-delay: 4s;
}
.lwrapter .lout .lin:nth-child(9) {
  width: 100px;
  height: 100px;
  left: 80%;
  animation-duration: 20s;
  -moz-animation-duration: 20s;
  -o-animation-duration: 20s;
  -webkit-animation-duration: 20s;
}
.lwrapter .lout .lin:nth-child(10) {
  width: 120px;
  height: 120px;
  left: 90%;
  animation-delay: 6s;
  animation-duration: 30s;
  -moz-animation-duration: 30s;
  -o-animation-duration: 30s;
  -webkit-animation-duration: 30s;
}
@keyframes square {
  0% {
    transform: translateY(0);
  }
  100% {
    bottom: 400px;
    transform: rotate(600deg);
    -webit-transform: rotate(600deg);
    transform: translateY(-500);
  }
}
